<template>
    <!--  ,  Bootstrap -->
    <a href="#" ref="scrollBtn" class="btn btn-primary position-fixed d-none"
        style="bottom: 151px; right: 18px; --bs-btn-font-size: 2.5rem; --bs-btn-active-bg: rgb(255 255 255 / 0%); --bs-btn-active-border-color: none; z-index: 1000; border-radius: 11px; width: 50px; height: 50px; display: flex; align-items: center; justify-content: center;"
        @click="scrollToTop">
        
    </a>
</template>

<script setup>
import { ref, onMounted, onUnmounted } from 'vue';

//    
const scrollBtn = ref(null);
let scrollHandler;

//  /   (Vue-)
const handleScroll = () => {
    if (window.scrollY > 100) {
        scrollBtn.value.classList.remove('d-none');
        scrollBtn.value.classList.add('show');  //    
    } else {
        scrollBtn.value.classList.add('d-none');
        scrollBtn.value.classList.remove('show');
    }
};

//   
const scrollToTop = (e) => {
    e.preventDefault();
    window.scrollTo({
        top: 0,
        behavior: 'smooth'
    });
};

//     (Lifecycle hooks)
onMounted(() => {
    scrollHandler = handleScroll;
    window.addEventListener('scroll', scrollHandler);
});

onUnmounted(() => {
    window.removeEventListener('scroll', scrollHandler);
});
</script>

<style scoped>
/*     Bootstrap  () */
#scrollToTop {
    transition: opacity 0.3s, transform 0.3s;
}

/* Hover-  CSS variables Bootstrap (   ) */
#scrollToTop:hover {
    transform: translateY(-2px);
    --bs-btn-bg: #0056b3;
    /*  primary */
}
</style>
