<script setup name="Offer">
//  nextTick  Vue,    DOM ( )
import { nextTick } from 'vue';

//         
const scrollToSafetyRules = () => {
    // ,  DOM    (  mounted  )
    nextTick(() => {
        const element = document.getElementById('safety-rules');
        if (element) {
            //     viewport
            const rect = element.getBoundingClientRect();
            //   :   +   - 20px (    20px  )
            const offsetTop = window.pageYOffset + rect.top - 110;
            //     
            window.scrollTo({ top: offsetTop, behavior: 'smooth' });
        }
    });
};
</script>
